import React, { Component } from 'react';
import {connect} from 'react-redux'
import '../css/slidingBar.css'
class NavBarHrz extends Component{
  constructor(props){
    super(props)
    this.state = { width : 0, margin: 0}
  }

  componentDidMount(){
    this.setState({width:this.refs.navBar.firstChild.offsetWidth, margin : this.refs.navBar.firstChild.offsetLeft - this.refs.navBar.offsetLeft})
    this.setState({width2:this.refs.navBar.firstChild.offsetWidth, margin2 : this.refs.navBar.firstChild.offsetLeft - this.refs.navBar.offsetLeft})
  }
  componentWillReceiveProps(nextProps){

    if(nextProps.slidePage.page !== this.props.slidePage.page)
    this.setState({ width2 : this.refs['presItem-'+nextProps.slidePage.page].offsetWidth, margin2 : this.refs['presItem-'+nextProps.slidePage.page].offsetLeft - this.refs.navBar.offsetLeft })

  }
  onHover(e){
    if(e.target.tagName === 'A')
      this.setState({ width : e.target.parentElement.offsetWidth, margin : e.target.parentElement.offsetLeft - this.refs.navBar.offsetLeft })
    else if (e.target.tagName === 'LI') {
      this.setState({ width : e.target.offsetWidth, margin : e.target.offsetLeft - this.refs.navBar.offsetLeft })

    }
  }
  onClickHandler(e, i){
    const { go } = this.props
    go(i);
    this.setState({ width2 : e.target.parentElement.offsetWidth, margin2 : e.target.parentElement.offsetLeft - this.refs.navBar.offsetLeft })
  }


  render(){
    const { go, listMenu, slidePage } = this.props
    const doList = listMenu.map((e, i)=>{
      return <li ref={'presItem-'+i} key={i} onClick={ (e)=> { this.onClickHandler(e, i)} } className={ i === slidePage.page ? 'active' : null }><a href='javascript: void(0)' >{e}</a></li>
    })
    return (
      <nav>
        <ul ref='navBar' onMouseOver={ this.onHover.bind(this) }>
          {doList}
        <hr id='slidingLine' style={{ 'width': this.state.width, 'marginLeft' : this.state.margin,'marginTop':'-0px','background':'#efefef' }} />
        <hr id='slidingLine2' style={{ 'width': this.state.width2, 'marginLeft' : this.state.margin2,'marginTop':'-3px' }} />
        </ul>
      </nav>
    )
  }
}

export default connect(
  state => ({slidePage : state.slidePage}),
  dispatch => ({
    go : (e) => { dispatch({type : 'CHANGE_PAGE', page : e }) }
  })
)(NavBarHrz)



