import React, { Component} from 'react'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import andrei from '../img/andrei.jpg';
import natalya from '../img/natalya.jpg';
import steffen from '../img/steffen.jpg';

import '../css/carousel.css'

class Carousel extends Component{
  constructor(props){
    super(props)
    this.state = {index : 0, timer : 0, delay: 800 }
  }
  componentWillMount(){
    this.setState({ index : 0})
  }
  componentDidMount(){

    let carIntv = setInterval( ()=> {
      var i = this.state.index;
        i > 1 ? i=0 : i++;
      this.setState({index : i})
 }, 5000);

  this.setState({timer : carIntv})

  }

  timer(){
    let carIntv = setInterval( ()=> {
      var i = this.state.index;
        i > 1 ? i=0 : i++;
      this.setState({index : i})
 }, 5000);

  this.setState({timer : carIntv})
  }

  render(){
    const urls = [ andrei, steffen, natalya]
    const indicator = urls.map((e, i)=>{
      var style;
      this.state.index == i ? style = {} : style = { 'background':'white' } //style active
      return <span key={'indic-'+i} style={ style } className='indicator-point' onClick={()=>{ this.setState({index : i, delay: 0 }); clearInterval(this.state.timer); this.timer() }}/>
    })
    return (
      <div>
      <ReactCSSTransitionGroup
      transitionEnterTimeout={800}
      transitionLeaveTimeout={800}
          transitionName={'carousel-slide'} style={{}}>
      <div key={this.state.index+'-carousel'} className='Carousel'>
        <img src={urls[this.state.index]} width='100%' height='100%'/>
      </div>
      </ ReactCSSTransitionGroup>
      <div className='indicator'>
        {indicator}
      </div>
      </div>

    )
  }
}

export default Carousel



