import React, { Component } from 'react';
import { connect } from 'react-redux';
import Carousel from './Carousel';
import logo from '../anaya.svg';


class Toppage extends Component {
  constructor(props) {
     super(props);
     this.state = { x: 0, y: 0, rx: 0, ry:0, contentHeight: 0, img: '../img/andrei.jpg' };
   }

   _onMouseMove(e) {
       this.setState({ x: e.screenX, y: e.screenY });
       let pageX = window.innerWidth;
       let pageY = window.innerHeight;
       let rotateY = (pageY-pageY/2-this.state.y)/pageY*5;
       let rotateX = -(pageX-pageX/2-this.state.x)/pageX*3;
       this.setState({ rx : rotateX, ry : rotateY })
     }
     componentDidMount(){
       this.setState({ contentHeight : this.refs.pagecontent.clientHeight });
     }

  render() {
    //var docHeight = Math.max( window.scrollHeight, window.offsetHeight, window.innerHeight,
    //               window.clientHeight, window.scrollHeight, window.offsetHeight );
    const winHeight = window.innerHeight
    const appHeight = (winHeight - this.state.contentHeight)/2
    const {scrollTop} = this.props

    var trsY = scrollTop*0.1
    var opacity = 1-((1+scrollTop*2)/winHeight)

    return (
      <div id='Toppage' style={{'height': (winHeight - appHeight)+'px', 'paddingTop': appHeight+'px'}} className={"App"} onMouseMove={this._onMouseMove.bind(this)}>
        <div ref='pagecontent'>
{/*          <div style={{ 'margin':'auto', 'transform' : 'translateX(0%) translateY(-0%) scale(1) perspective( 1960px ) rotateY('+this.state.rx+'deg) rotateX('+this.state.ry+'deg)' }}> */}
          <div style={{ 'margin':'auto','opacity':opacity , 'transform' : 'translateX('+this.state.rx+'px) translateY('+trsY+'%) scale(1) perspective( 1960px ) rotateY('+this.state.rx*1.3+'deg) rotateX(0deg)','transition':'transform 0.1s ease-out' }}>
            <img src={logo} className="App-logo" alt="logo" />
          </div>
          <p className="App-intro" >
           Invoque moi ,et je te rpondrai, je tannoncerai de grandes choses, des choses caches que tu ne connais pas.<br/>
  Jeremie 33. 3
          </p>
        </div>
        <Carousel />
      </div>

    );
  }
}


export default connect(
  state => ({scrollTop : state.currentScroll.scroll}),
  dispatch => ({})
)(Toppage)



