const minimizeReducer = (state ={ anime : 0}, action) =>{
  switch (action.type) {
    case 'LOGO_MINIMIZE':
      return { anime: action.anime, scrollDirection: action.scrollDirection}
    case 'GET_CURRENT_DIRECTION':
      return {...state, scroll: action.scrollTop, scrollDirection: action.scrollDirection }
    default:
      return state
  }
}

export default minimizeReducer



