const NavVrtReducer = ( state={ currentPage: '' }, action ) => {
  switch (action.type) {
    case 'GET_CURRENT_PAGE':
      return { currentPage : action.current, id: action.id }
    default:
      return state
  }

}

export default NavVrtReducer



