const navBarMiddleware = store => next => action => {
 var sens

(store.getState().slidePage.page - action.page < 0) ? sens = 'slide' : sens = 'rslide'

  if (action.type === 'GET_FIRST_SCROLL') { // initiatePostion start
    if(action.scrollTop > action.winHeight/2+40)
      {return next({type:'LOGO_MINIMIZE',anime:1,scrollDirection : 'DOWN'})}
    else{  return next({type:'LOGO_MINIMIZE',anime:0, scrollDirection : 'UP'})}
  }

  if(action.type === 'GET_CURRENT_SCROLL'){
    if( action.scrollTop < action.winHeight/2+40 && store.getState().anime.scrollDirection === 'DOWN' ){
        return next({type:'LOGO_MINIMIZE',anime:0, scrollTop: action.scrollTop, scrollDirection : 'UP'})
    }
    if( action.scrollTop > action.winHeight/2+40 && store.getState().anime.scrollDirection === 'UP'){
        return next({type:'LOGO_MINIMIZE',anime:1, scrollTop: action.scrollTop, scrollDirection : 'DOWN'})
    }
    return next(action)
  }

  switch (action.type) {
    case 'CHANGE_PAGE':
      return next({...action, sens : sens})
    case 'GET_CURRENT_PAGE':
      return next(action)
    default:
      return next(action)
  }
return next(action)
}

export default navBarMiddleware



