import React, {Component} from 'react'

export default class Description extends Component {
  constructor(){
    super()
    this.state ={width : 400}
  }
  componentDidMount(){
    const {container, img}= this.refs
    this.setState({width: container.clientWidth+ img.clientWidth, height: img.clientHeight})
  }
  render(){
    return (
        <div style={{'position':'relative','margin':'auto','top':'50px', 'maxWidth':'90%'}}>

          <div ref='img' style={{'display':'inline-block', 'zIndex':'0', 'background':'#F7F2E0'}} className="miniRect">
          </div>
          <div ref='container' style={{...Style.container}}>
          <h1 style={{'zIndex':'2','fontSize':'2em','color':'blue', 'position':'relative', 'transform':'translateX(-30%)'}}> {this.props.title} <br /> lorem ipsum</h1>
            <div style={Style.contentText} >
              <hr style={Style.lineHr} />
              <p> Lorem ipsum dolor sit amet, consectetur adipisicing elit. Eligendi non quis exercitationem culpa nesciunt nihil aut nostrum <br/><br/>
              Explicabo reprehenderit optio amet ab temporibus asperiores quasi cupiditate. Voluptatum ducimus voluptates voluptas?</p>
            </div>
          </div>
        </div>

    )
  }
}

const Style = {
  lineHr : {'margin':'0', 'border':'none','width':'10%','height':'3px','background':'blue'},
  container : {'marginLeft':'50px','display':'inline-block','textAlign':'left','maxWidth':'350px','position':'relative','zIndex':'1'},
  contentText : {'fontSize':'0.8em'}
}



