import React, { Component } from 'react';
import {connect} from 'react-redux'
import Slide from './Slide'
import Components from '../ContentPage/index'
import NavBarHrz from './NavBarHrz'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';

class ContentPage extends Component {

  constructor(props){
    super(props);
    this.state = { content : this.props.slidePage.page, sens: this.props.slidePage.sens} //index nav
  }

  render(){
    const { slidePage, color, id } = this.props
    const content = ['Description','Chorale','Foot', 'Solidaire']
    var show = content[slidePage.page]
    var Acomponent = Components[show]

  return (
    <div id={id} className={'App '+ this.props.className} style={{'height': window.innerHeight}}>
      <div className='Container'>
        <div className='App-title'> {id} </div>
        <NavBarHrz listMenu={ content }/>
        <Slide color={show} page={  <Acomponent title={show}/> /*on concat le composant voulu*/ } sens={slidePage.sens}/>
      </div>
    </div>
  )}
}

export default connect(
  state => ({ slidePage : state.slidePage}),
  dispatch => ({  })
)(ContentPage)



