import React, { Component } from 'react'
import {connect} from 'react-redux'
import logo from '../mini.svg'
import '../css/Bar.css'
import SocialNetwork from '../img/svg'

class Bar extends Component {
  constructor(){
    super()
    this.state= { barHeight : 0}
  }

  componentDidMount(){
    this.setState({positionLogo: (this.refs.bar.clientHeight/2)*0.5, positionLogoX: (this.refs.bar.clientWidth-300-this.refs.minilogo.clientWidth)/2 })
  }
  render(){
    const { anime, scrollTop }= this.props
    let scale
    if(anime.anime === 0)
      {
        scale = 0.8
      }
    else {
      scale = 2
    }
    return(
    <div ref='bar' className='bar'>
      <img ref='minilogo' src={logo} style={{'top':+this.state.positionLogo+'px', 'right':this.state.positionLogoX+'px','opacity':anime.anime,'transform': 'scale('+anime.anime+')'}} className={"App-mini "} alt="minilogo" />
      <ul className='socialNetwork'>

        <li><img src={ SocialNetwork.facebook } /></li>
        <li><img src={ SocialNetwork.twitter } /></li>
        <li><img src={ SocialNetwork.insta } alt='insta'/></li>
        <li><img src={ SocialNetwork.pinterest } /></li>
      </ul>
    </div>
  )
  }
}

export default connect(
state => ({ anime : state.anime, scrollTop : state.currentScroll.scroll }),
dispatch => ({})

)(Bar)



