import React ,{ Component } from 'react';
import { connect } from 'react-redux'
import Toppage from './Components/Toppage'
import NavVrt from './Components/NavVrt'
import Bar from './Components/Bar'
import ContentPage from './Components/ContentPage'
import './css/App.css'

class App extends Component {
  constructor(props){
    super(props)
    this.state = { pages : [], currentPage : 'Home' }
  }

  componentDidMount() {
    var winHeight = window.innerHeight;
    var currentOffset = window.pageYOffset
    window.addEventListener('scroll', this.handleScroll.bind(this));
    this.setState({ pages : Array.from(document.getElementsByClassName('Pages')) })
    this.props.dispatch({type: 'GET_FIRST_SCROLL', scrollTop: currentOffset, winHeight: winHeight})
  }

  handleScroll(){
    var winHeight = window.innerHeight;
    // var body = document.body;
    // var html = document.documentElement;
    // var docHeight = Math.max( body.scrollHeight, body.offsetHeight,
    //                html.clientHeight, html.scrollHeight, html.offsetHeight );
    var currentOffset = window.pageYOffset


    this.props.dispatch({type: 'GET_CURRENT_SCROLL', scrollTop: currentOffset, winHeight: winHeight})

    var currentPage = this.state.pages.map((e, i) =>{
      if( e.id === 'Prsentation')
        if( currentOffset < e.offsetTop-200 )
          this.props.dispatch({type: 'GET_CURRENT_PAGE', current : null})
      if(currentOffset + 200 > e.offsetTop)
        this.props.dispatch({type: 'GET_CURRENT_PAGE', current : e.id, id: i+1})
    })



    // if(currentOffset === this.state.pages[1].offsetTop)
    //   this.props.dispatch({type: 'GET_CURRENT_PAGE', current : this.state.pages[1].id})

  }

render(){
  return(
    <div className='App full'>
      <Toppage color='blue' className='Pages'/>
      <ContentPage color='' id='Prsentation' className='Pages'/>
      <NavVrt />
      <Bar/>
    </div>
)
}
}

export default connect(
  state => state,
  dispatch => ({dispatch})
)(App);



