import React, {Component} from 'react'
import {connect} from 'react-redux'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';


class Slide extends Component {
  constructor(props){
    super(props)
    this.state = { width : '200%' }
  }

  render(){
    return (
      <ReactCSSTransitionGroup
      transitionEnterTimeout={300}
      transitionLeaveTimeout={300}
          transitionName={this.props.sens} style={{'verticalAlign':'middle','textAlign':'left','display':'inline-block', 'width':this.state.width}}>
          <div style={Style.arrowL} onClick={ ()=> { (this.props.slidePage.page-1 > -1) ? this.props.go(this.props.slidePage.page-1): null;} }>
            <span style={Style.span}>{'<'}</span>
          </div>
          <div key={this.props.color} className={"rect " + this.props.color } >
            {this.props.page}
          </div>
          <div style={Style.arrowR} onClick={ ()=> { (this.props.slidePage.page+1 < 4) ? this.props.go(this.props.slidePage.page+1): null } }>
            <span style={Style.span}>{'>'}</span>
          </div>
      </ReactCSSTransitionGroup>

    )
  }
}

const Style = {
  arrowL : {'zIndex':3,'display':'inline','cursor':'pointer','position':'absolute','width':'20px','top':'50%','left':'10%'},
  arrowR : {'zIndex':3,'display':'inline','cursor':'pointer','position':'absolute','width':'20px','top':'50%','left':'90%'},
  span : { 'fontSize':'4em'}
}

export default connect(
  state => ({ slidePage : state.slidePage }),
  dispatch => ({
    go : (e) => { dispatch({type : 'CHANGE_PAGE', page : e }) }
  })
)(Slide)



