const ScrollReducer = (state = {scroll :0}, action) =>{
  switch (action.type) {
    case 'GET_CURRENT_SCROLL':
      return {...state, scroll : action.scrollTop}
    case 'GET_FIRST_SCROLL':
      return { scrollDirection: action.scrollDirection }
    default:
      return state
  }
}

export default ScrollReducer



