import React, { Component } from 'react';
import {connect} from 'react-redux'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import '../css/slidingBar.css'

class NavVrt extends Component{
  constructor(props){
    super(props)
  }

  render(){
    const { currentPage } = this.props
    return (
      <div className='NavVrt'>
        <span className='titlePage'>{ currentPage.currentPage }</span>
      <span className='numPage' style={currentPage.id ? {'display':'inline-block'} : {'display':'none'}}>{ currentPage.id }</span>
      </div>
    )
  }
}

export default connect(
  state => ({currentPage : state.currentPage}),
  dispatch => ({
    go : (e) => { dispatch({type : 'CHANGE_PAGE', page : e }) }
  })
)(NavVrt)



